.table {
  background-color: a;
  border-collapse: collapse;
  border: 2px solid black;
  letter-spacing: 1px;
}

td,
th {
  border: 1px solid rgb(190, 190, 190);
  padding: 10px 20px;
}
th {
  background-color: rgb(182, 250, 250);
}
td {
  text-align: center;
}

tr:nth-child(even) td {
  background-color: rgb(250, 250, 250);
}

tr:nth-child(odd) td {
  background-color: rgb(245, 245, 245);
}

caption {
  padding: 10px;
}
